.filter {
  margin-top: 6.25rem;
}
.filter__list {
  display: flex;
  flex-wrap: wrap;
  width: 100%;
}
.filter__item button {
  margin-bottom: 1.375rem;
  padding: 0 1.5rem;
  color: rgba(108, 108, 108, 0.86);
  font-weight: bold;
  font-size: 2.25rem;
  line-height: 3rem;
  background: none;
  border: 0;
  cursor: pointer;
  transition: all linear 0.09s;
}
.filter__item button:hover, .filter__item button.active {
  color: var(--accent-color);
}