:root {
  --font-color: #373737;
  --accent-color: #1275d3;
  --background-color:#FAFAFA;
}

.header {
  padding: 1.375rem 0;
  box-shadow: 0px 0.125rem 0.25rem rgba(208, 208, 208, 0.25);
}
@media (max-width: 647px) {
  .header {
    padding: 15px 0;
  }
}
.header .container {
  display: flex;
  align-items: center;
  justify-content: space-between;
  max-width: min(1550px, 85vw);
}
@media (max-width: 647px) {
  .header .container {
    max-width: 100%;
  }
}
@media (max-width: 647px) {
  .header__logo {
    width: 150px;
    max-width: 50%;
  }
}
.header__logo img {
  max-width: 100%;
  height: 3.25rem;
}
.header twirl-burger {
  display: none;
  margin-left: 20px;
  color: var(--accent-color);
}
@media (max-width: 647px) {
  .header twirl-burger {
    display: block;
  }
}

.menu__list {
  display: flex;
  align-items: center;
}
.menu__item {
  color: var(--font-color);
  font-size: 1.5rem;
  line-height: 1.75rem;
}
.menu__item:not(:last-child) {
  margin-right: 3.75rem;
}
@media (max-width: 647px) {
  .menu__item:not(:last-child) {
    margin-right: 0;
  }
}
.menu__item a:not(.btn) {
  display: inline-block;
  color: inherit;
  text-decoration: none;
  transition: color linear 0.1s;
}
@media (max-width: 647px) {
  .menu__item a:not(.btn) {
    display: none;
  }
}
.menu__item a:not(.btn):hover {
  color: var(--accent-color);
}
@media (max-width: 400px) {
  .menu__item a.btn {
    display: none;
  }
}